<script setup lang="ts">
import { computed } from "vue";
import { ABtn, ACol, ASkeletonLoader, MESSAGES } from "../index";

const props = withDefaults(
  defineProps<{
    pageIndex: number;
    pageCount: number;
    isLoading?: boolean;
  }>(),
  {
    isLoading: false,
  },
);

const emit = defineEmits<{
  (event: "update:page-index", value: number): void;
}>();

const selectedPageIndex = computed({
  get() {
    return (props.pageIndex ?? 0) + 1;
  },
  set(value: number) {
    emit("update:page-index", value - 1);
  },
});

const isFirstPage = computed(() => selectedPageIndex.value === 1);
const isLastPage = computed(() => selectedPageIndex.value === props.pageCount);
</script>

<template>
  <ACol
    class="a-pagination-bar d-flex align-center justify-center pa-0"
    cols="0"
  >
    <ABtn
      :disabled="isFirstPage"
      variant="text"
      icon="first_page"
      class="mr-2"
      :show-skeleton="isLoading"
      @click="selectedPageIndex = 1"
    />
    <ABtn
      :disabled="isFirstPage"
      variant="text"
      icon="navigate_before"
      class="mr-2"
      :show-skeleton="isLoading"
      @click="selectedPageIndex -= 1"
    />
    <ASkeletonLoader :loading="isLoading" width="90px" height="24px">
      <span>
        {{
          `${selectedPageIndex || 1} ${MESSAGES.OF} ${pageCount || 1} ${
            MESSAGES.PAGES
          }`
        }}
      </span>
    </ASkeletonLoader>
    <ABtn
      :disabled="isLastPage"
      variant="text"
      icon="navigate_next"
      class="mx-2"
      :show-skeleton="isLoading"
      @click="selectedPageIndex += 1"
    />
    <ABtn
      :disabled="isLastPage"
      variant="text"
      icon="last_page"
      class="mr-2"
      :show-skeleton="isLoading"
      @click="selectedPageIndex = pageCount"
    />
  </ACol>
</template>
