<script setup lang="ts">
import ASkeletonLoader from "../ASkeletonLoader/ASkeletonLoader.vue";
import { computed } from "vue";

const props = withDefaults(
  defineProps<{
    loading?: boolean;
    density?: string;
    readonly?: boolean;
    bgColor?: string;
    hideDetails?: boolean | "auto";
    rows?: number | string;
  }>(),
  {
    loading: false,
    density: "compact",
    readonly: false,
    bgColor: "rgb(var(--v-theme-field_bg))",
    hideDetails: "auto",
    rows: 5,
  },
);

const updatedProps = computed(() => ({
  ...props,
  bgColor: props.readonly ? "transparent" : props.bgColor,
}));
</script>

<template>
  <ASkeletonLoader
    v-if="loading"
    class="a-input v-input"
    :height="`${rows}lh`"
  />
  <v-textarea v-else class="a-textarea" v-bind="updatedProps">
    <template v-for="(_, slot) of $slots" #[slot]="scope">
      <slot :key="slot" :name="slot" v-bind="scope" />
    </template>
  </v-textarea>
</template>

<style lang="scss">
.a-textarea {
  .v-field--variant-plain,
  .v-field--variant-filled .v-field__outline::before,
  .v-field--variant-underlined .v-field__outline::before {
    border-style: dashed;
    border-width: 0 0 0 var(--v-field-border-width);
  }
}
</style>
