<script setup lang="ts">
import { computed } from "vue";
import type { IAction, IActionsProps } from "./ACreateBtn.types";
import type { IMenuItem as IDropdownMenuItem } from "../ADropdown";
import { ABtn, AMenu, AList, AListItem, ADropdown } from "../index";

const props = withDefaults(
  defineProps<{
    createActions?: IAction[] | boolean;
    actionsProps?: IActionsProps;
    menuColor?: string;
    overlayColor?: string;
    overlayOpacity?: string | number;
    iconSize?: string;
    icon?: boolean;
    caption?: string;
  }>(),
  {
    createActions: undefined,
    actionsProps: undefined,
    menuColor: "transparent",
    overlayColor: undefined,
    overlayOpacity: undefined,
    iconSize: "x-large",
    icon: true,
    caption: "",
  },
);

defineEmits<{
  (event: "create", eventName?: string): void;
}>();

const singleAction = computed(() => {
  if (Array.isArray(props.createActions) && props.createActions.length === 1) {
    return props.createActions[0];
  }

  if (props.createActions === true) {
    return {
      event: "add-row",
    };
  }

  return null;
});
</script>

<template>
  <template v-if="createActions">
    <template v-if="icon">
      <ABtn
        v-if="singleAction"
        color="primary"
        :icon="singleAction.icon || 'add'"
        :size="iconSize"
        class="a-create-btn"
        :title="singleAction.title"
        v-bind="$attrs"
        @click="$emit('create', singleAction.event)"
      />
      <AMenu
        v-if="Array.isArray(createActions) && createActions?.length > 1"
        color="error"
        class="a-create-btn-menu"
        theme="light"
        :style="{
          '--overlay-color': `var(--v-theme-${overlayColor || 'bg'})`,
          '--overlay-opacity': overlayOpacity || '0',
        }"
      >
        <template #activator="{ props: btnProps }">
          <ABtn
            color="primary"
            icon="add"
            :size="iconSize"
            class="a-create-btn"
            v-bind="{ ...btnProps, ...$attrs }"
          />
        </template>
        <AList :class="menuColor && `bg-${menuColor}`">
          <AListItem
            v-for="(item, index) of createActions as IAction[]"
            :key="index"
            :value="index"
          >
            <ABtn
              :prepend-icon="item.icon"
              :append-icon="item.appendIcon"
              :href="item.href"
              :disabled="item.disabled"
              :loading="item.loading"
              variant="flat"
              block
              :text="$t(item.title)"
              v-bind="actionsProps"
              @click="$emit('create', item.event)"
            />
          </AListItem>
        </AList>
      </AMenu>
    </template>
    <template v-else>
      <ABtn
        v-if="singleAction"
        :prepend-icon="singleAction.icon"
        :size="iconSize"
        class="a-create-btn"
        :text="caption || singleAction.title"
        v-bind="$attrs"
        @click="$emit('create', singleAction.event)"
      />
      <ADropdown
        v-else
        :caption="String(caption)"
        :data="createActions as IDropdownMenuItem[]"
      />
    </template>
  </template>
</template>

<style lang="scss">
.a-create-btn {
  i {
    transition-duration: 0.3s;
    transition-property: transform;
  }

  &[aria-expanded="true"] i {
    transform: rotate(45deg);
  }
}

.v-menu.a-create-btn-menu {
  &::before {
    content: " ";
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background: rgb(var(--overlay-color));
    opacity: var(--overlay-opacity);
  }

  .v-list-item__overlay {
    --v-hover-opacity: 0;
  }

  .v-overlay__content {
    .a-list.v-list {
      --list-background: transparent;
      box-shadow: unset !important;
      width: max-content;

      .v-list-item {
        .v-list-item__content {
          overflow: visible;
        }

        &:hover {
          background: transparent;
        }
      }
    }
  }
}
</style>
