<script setup lang="ts">
import { computed, ref, useAttrs, type Ref } from "vue";
import { MESSAGES } from "../constants";
import ASkeletonLoader from "../ASkeletonLoader/ASkeletonLoader.vue";
import type { VTextField } from "vuetify/components";

defineOptions({ inheritAttrs: false });

const props = withDefaults(
  defineProps<{
    modelValue?: string | number;
    modelModifiers?: Record<string, boolean>;
    color?: string;
    density?: string;
    hideDetails?: boolean | "auto";
    readonly?: boolean;
    transparent?: boolean;
    preserveHeight?: boolean;
    variant?: string;
    placeholder?: string;
    persistentPlaceholder?: boolean;
    required?: boolean;
    hideMandatory?: boolean;
    changed?: boolean;
    loading?: boolean;
    autofocus?: boolean;
    persistVariant?: boolean;
  }>(),
  {
    modelValue: "",
    modelModifiers: () => ({}),
    color: "accent",
    density: "compact",
    hideDetails: "auto",
    preserveHeight: false,
    variant: "outlined",
    placeholder: "",
    persistentPlaceholder: false,
    required: false,
    hideMandatory: false,
    changed: false,
    loading: false,
    autofocus: false,
    persistVariant: false,
  },
);

const emit = defineEmits<{
  (event: "update:modelValue", value: string): void;
}>();

const textFieldRef: Ref<VTextField | undefined> = ref();

const attrs = useAttrs();

const updatedProps = computed(() => ({
  ...attrs,
  ...props,
  class: [
    "a-input",
    "a-text-field",
    {
      "v-input--filled": !props.transparent,
      "a-input--preserve-height": props.preserveHeight,
      "a-input--changed": props.changed,
      "a-input--required":
        !props.hideMandatory && props.required && !props.readonly,
    },
    attrs.class,
  ],
  variant: props.persistVariant || !props.readonly ? props.variant : "plain",
  placeholder: props.readonly ? MESSAGES.MANDATORY_FIELD : props.placeholder,
  persistentPlaceholder: props.readonly || props.persistentPlaceholder,
}));

const innerInput = ref("");

const handleChange = ({ target }: Event) => {
  if (props.modelModifiers?.lazy) {
    const { value } = target as HTMLInputElement;
    emit("update:modelValue", value);
  }
};
const handleInput = (text: string) => {
  innerInput.value = text;
  if (!props.modelModifiers?.lazy) {
    emit("update:modelValue", text);
  }
};
const handleEnter = () => {
  if (!props.modelModifiers?.lazy) {
    emit("update:modelValue", innerInput.value);
  }
};
const handleClear = () => {
  if (props.modelModifiers?.lazy) {
    innerInput.value = "";
    emit("update:modelValue", "");
  }
};

function focus() {
  (textFieldRef.value?.$el as HTMLDivElement)
    .querySelector<HTMLInputElement>("input")
    ?.focus();
}

defineExpose({
  focus,
});
</script>

<template>
  <div v-if="loading">
    <ASkeletonLoader
      :class="'a-input--skeleton-' + density"
      class="a-input v-input"
    />
  </div>
  <v-text-field
    v-show="!loading"
    ref="textFieldRef"
    v-bind="updatedProps"
    @keypress.enter="handleEnter"
    @change="handleChange"
    @click:clear="handleClear"
    @update:modelValue="handleInput"
  >
    <template v-for="(_, slot) of $slots" #[slot]="scope">
      <slot :key="slot" :name="slot" v-bind="scope" />
    </template>
  </v-text-field>
</template>

<style lang="scss">
.v-text-field.v-input {
  position: relative;
  color: rgb(var(--v-theme-field_color));

  &.a-input--changed {
    color: rgb(var(--v-theme-field_color_changed));
    .v-field__outline {
      --v-theme-field_outline: var(--v-theme-field_color_changed);
    }
  }

  &--disabled {
    opacity: 1;
    color: rgb(var(--v-theme-disabled));
  }

  .v-label {
    color: rgb(var(--v-theme-field_label));
    --v-medium-emphasis-opacity: 1;
  }

  &--filled .v-field--variant-outlined {
    background-color: rgb(var(--v-theme-field_bg));
  }

  &.a-input--required {
    .v-field-label:after {
      content: "*";
      margin-left: 4px;
      color: rgb(var(--v-theme-active_secondary_5));
    }
  }

  &--readonly {
    input::placeholder {
      font-style: italic;
      opacity: 1;
      color: rgb(var(--v-theme-field_label));
    }
    .v-label.v-field-label {
      top: 7px;
    }
    .v-field--variant-plain.v-field,
    .v-field--variant-underlined.v-field {
      --v-field-padding-top: 0px;
      --v-field-padding-bottom: 8px;
    }
  }

  .v-input__details {
    top: -4px;
    margin-bottom: 0;
    padding-top: 4px;
  }

  &.a-input--preserve-height {
    .v-input__details {
      position: absolute;
    }
  }

  .v-field {
    .v-chip {
      background-color: rgb(var(--v-theme-background_3));

      &__underlay {
        opacity: 0;
      }
    }

    &:not(&--error, &--disabled) .v-field__outline {
      --v-field-border-opacity: 1;
      color: rgb(var(--v-theme-field_outline));
    }

    .v-icon {
      --v-medium-emphasis-opacity: 1;
      color: rgb(var(--v-theme-field_icon));
    }
  }
}

.a-input--skeleton {
  &-compact {
    box-sizing: content-box;
    height: 20px;
    margin: 10px 0;
    border-radius: 100px;
  }
  &-default {
    box-sizing: content-box;
    height: 20px;
    margin: 18px 0;
    border-radius: 100px;
  }
}
</style>
