<script setup lang="ts">
/* eslint-disable @typescript-eslint/no-explicit-any */
import { computed, ref, useAttrs, useSlots, watch } from "vue";
import type { IDataFetcher } from "@/services/api/api.models";
import {
  ABtn,
  AList,
  AMenu,
  AListItem,
  ATextField,
  ASearchField,
  APaginationBar,
  ASkeletonLoader,
  LOCATION,
  DEFAULT_PAGE_SIZE,
  type IMenuItem,
} from "..";

const props = withDefaults(
  defineProps<{
    modelValue?: boolean;
    text?: string;
    prependText?: boolean;
    fab?: boolean | string;
    search?: boolean;
    searchLazy?: boolean;
    searchLabel?: string;
    pageSize?: boolean | number | string;
    variant?: string;
    data: IMenuItem[] | IDataFetcher<IMenuItem>;
    location?: LOCATION;
    eager?: boolean;
    menuWidth?: string | number;
    loading?: boolean;
    itemTitle?: string;
    block?: boolean;
    select?: boolean;
  }>(),
  {
    modelValue: false,
    text: "",
    fab: false,
    search: false,
    searchLazy: false,
    searchLabel: undefined,
    pageSize: false,
    variant: undefined,
    location: undefined,
    eager: false,
    menuWidth: "350px",
    loading: false,
    itemTitle: "title",
    select: false,
  },
);

const emit = defineEmits<{
  (event: "event", eventName?: string, item: IMenuItem): void;
  (event: "update:modelValue", value: boolean): void;
}>();

const attrs = useAttrs();
const slots = useSlots();

const isFetched = ref(false);
const isMenuOpened = ref(props.modelValue);
const isLocal = computed(() => typeof props.data !== "function");

const _data = ref(Array.isArray(props.data) ? [...props.data] : []);
const searchText = ref("");
const isLoading = ref(false);

const localPageSize = computed(() => {
  if (!props.pageSize) {
    return 0;
  }

  return props.pageSize === true ? DEFAULT_PAGE_SIZE : Number(props.pageSize);
});
const showPagination = computed(() => localPageSize.value > 0);
const pageIndex = ref(0);
const _rowsCount = ref(1);

watch(
  () => props.modelValue,
  (value) => {
    isMenuOpened.value = value;
  },
);
watch(isMenuOpened, () => {
  if (isMenuOpened.value !== props.modelValue) {
    emit("update:modelValue", isMenuOpened.value);
  }
});
watch(
  () => props.data as IMenuItem[],
  (items) => {
    _data.value = Array.isArray(items) ? [...items] : [];
  },
);

const filteredItems = computed(() => {
  if (!searchText.value || !isLocal.value) {
    return _data.value;
  }

  const searchValue = searchText.value.toLowerCase();
  return _data.value.filter((item) =>
    String(item[props.itemTitle as keyof IMenuItem])
      .toLowerCase()
      .includes(searchValue),
  );
});
const pageItems = computed(() => {
  if (!showPagination.value || !isLocal.value) {
    return filteredItems.value;
  }

  const pageStart = pageIndex.value * localPageSize.value;
  return filteredItems.value.slice(pageStart, pageStart + localPageSize.value);
});
const pageCount = computed(() => {
  if (!showPagination.value) {
    return 1;
  }

  const rowCount = isLocal.value
    ? filteredItems.value.length
    : _rowsCount.value;
  return Math.max(Math.ceil(rowCount / localPageSize.value), 1);
});

const skeletonCount = computed(
  () => pageItems.value.length || DEFAULT_PAGE_SIZE,
);

const iconPosition = computed(() => {
  if (props.select) {
    return props.prependText ? "appendInnerIcon" : "prependInnerIcon";
  }
  return props.prependText ? "appendIcon" : "prependIcon";
});

const updatedProps = computed(() => {
  let icon;
  if (props.fab) {
    icon = props.fab === true ? "add" : props.fab;
  }
  const location = props.location || (props.fab ? "end" : "bottom");

  const arrowIcon = isMenuOpened.value ? "arrow_drop_up" : "arrow_drop_down";
  const captionFieldName = props.select ? "modelValue" : "text";
  const variant = props.variant || (props.select ? "outlined" : "text");

  return {
    [iconPosition.value]: arrowIcon,
    [captionFieldName]: props.text,
    variant,
    icon,
    location,
    ...attrs,
  };
});

const showSearchInput = computed(() => props.search || props.searchLazy);
const showPrependSlot = computed(
  () => Boolean(slots["prepend-item"]) || showSearchInput.value,
);
const showAppendSlot = computed(
  () => Boolean(slots["append-item"]) || showPagination.value,
);

const listClass = computed(() => {
  switch (props.location) {
    case LOCATION.START:
    case LOCATION.END:
      return "mx-1";
    default:
      return "my-1";
  }
});

const handleClick = (item: IMenuItem) => {
  if (item.callback) {
    item.callback();
  } else {
    emit("event", item.event, item);
  }
};

watch(pageCount, (count) => {
  if (count <= pageIndex.value) {
    pageIndex.value = count - 1;
  }
});

const fetchData = async (query: {
  query: string;
  start: number;
  limit: number;
}) => {
  if (isLocal.value) {
    return;
  }

  try {
    isFetched.value = true;
    isLoading.value = true;

    const api = props.data as IDataFetcher<IMenuItem>;
    const { data, n_rows } = await api(query);
    _data.value = data;
    _rowsCount.value = n_rows;
  } catch (err) {
    // eslint-disable-next-line no-console
    console.error((err as Error).message);
  } finally {
    isLoading.value = false;
  }
};

const getQuery = () => {
  const limit = localPageSize.value ?? DEFAULT_PAGE_SIZE;
  const start = limit * pageIndex.value;

  return {
    query: searchText.value,
    start,
    limit,
  };
};

watch(getQuery, fetchData);

watch(isMenuOpened, (isShown) => {
  if (!isShown) {
    return;
  }

  if (isLocal.value) {
    pageIndex.value = 0;
    _data.value = [...(props.data as IMenuItem[])];
    return;
  }

  if (!props.eager && isFetched.value) {
    return;
  }

  _data.value = [];
  _rowsCount.value = 0;
  const isChanged = pageIndex.value || searchText.value;
  if (isChanged) {
    pageIndex.value = 0;
    searchText.value = "";
  } else {
    fetchData(getQuery());
  }
});
</script>

<template>
  <div :style="{ display: block ? 'block' : 'inline-block' }">
    <AMenu
      v-model="isMenuOpened"
      class="a-dropdown__menu"
      :location="location"
      :width="menuWidth"
    >
      <template #activator="{ props: btnProps }">
        <slot name="activator">
          <ATextField
            v-if="select"
            class="a-dropdown__activator"
            readonly
            persist-variant
            v-bind="{ ...btnProps, ...updatedProps }"
          />
          <ABtn
            v-else
            class="a-dropdown__activator"
            v-bind="{ ...btnProps, ...updatedProps }"
          />
        </slot>
      </template>
      <AList rounded="lg" :class="listClass">
        <template v-if="showPrependSlot">
          <div class="a-list-item__sticky a-list-item__skipped">
            <v-list-item v-if="showSearchInput">
              <ASearchField
                v-model="searchText"
                :model-modifiers="{ lazy: searchLazy }"
                :label="searchLabel"
                theme="light"
                autofocus
                @click.stop
              />
            </v-list-item>

            <slot name="prepend-item" />
          </div>
        </template>

        <template v-if="isLoading || loading">
          <div
            v-for="index in skeletonCount"
            :key="index"
            class="a-list-item__skipped"
          >
            <slot name="loader">
              <AListItem class="py-0">
                <ASkeletonLoader width="100%" height="20px" />
              </AListItem>
            </slot>
          </div>
        </template>
        <template
          v-for="item of pageItems"
          v-else
          :key="item[props.itemTitle as keyof IMenuItem]"
        >
          <slot
            name="item"
            :item-title="itemTitle"
            :item="item"
            @click="handleClick(item)"
          >
            <AListItem
              class="py-0"
              :title="item[itemTitle]"
              v-bind="item"
              @click="handleClick(item)"
            />
          </slot>
        </template>

        <template v-if="showAppendSlot">
          <div class="a-list-item__sticky a-list-item__skipped">
            <slot name="append-item" />

            <v-list-item v-if="showPagination">
              <APaginationBar
                v-model:page-count="pageCount"
                v-model:page-index="pageIndex"
                @click.stop
              />
            </v-list-item>
          </div>
        </template>
      </AList>
    </AMenu>
  </div>
</template>

<style lang="scss">
.a-dropdown {
  &__menu {
    .a-text-field.v-input {
      color: rgb(var(--v-theme-on_light));
    }
    .a-checkbox {
      --v-theme-checkbox_icon: var(--v-theme-surface);

      .v-selection-control {
        --v-selection-control-size: 30px;

        .v-icon {
          margin-right: 8px;
        }
      }
    }
  }
}
</style>
