import { computed } from "vue";

interface IInputProps {
  disabled?: boolean;
  readonly?: boolean;
  rounded?: boolean;
  modelValue?: boolean;
  indeterminate?: boolean;
}

export const useInputColor = (props: IInputProps) => {
  return computed(() => {
    if (props.disabled) {
      return "disabled";
    }

    if (props.readonly) {
      return !props.modelValue && props.rounded && !props.indeterminate
        ? "background"
        : "checkbox_icon";
    }

    if (
      props.indeterminate ||
      (props.modelValue && !props.rounded) ||
      (!props.modelValue && props.rounded)
    ) {
      return "primary";
    }

    return "checkbox_icon";
  });
};

export const useInputBgColor = (props: IInputProps) => {
  return computed(() => {
    if (!props.disabled && props.rounded && !props.indeterminate) {
      if (props.modelValue) {
        return "linear-gradient(90deg, #00efbf 0%, #00daee 100%)";
      }
      return props.readonly
        ? "rgba(var(--v-theme-on-background))"
        : "transparent";
    }

    return "transparent";
  });
};
