<script setup lang="ts">
/* eslint-disable @typescript-eslint/no-explicit-any */
import { computed, ref, watch } from "vue";
import {
  ASkeletonLoader,
  AChip,
  ASearchField,
  APaginationBar,
  useSelect,
  DEFAULT_PAGE_SIZE,
  type IListProps,
  type IItem,
} from "../index";

interface ISelectProps extends IListProps {
  color?: string;
  density?: "default" | "comfortable" | "compact";
  readonly?: boolean;
  transparent?: boolean;
  variant?:
    | "outlined"
    | "plain"
    | "underlined"
    | "filled"
    | "solo"
    | "solo-inverted"
    | "solo-filled";
  placeholder?: string;
  persistentPlaceholder?: boolean;
  required?: boolean;
  hideMandatory?: boolean;
  hideNoData?: boolean;
  hideDetails?: boolean | string;
  changed?: boolean;
  loading?: boolean;
  multiple?: boolean;
  chips?: boolean;
  modelValue?: any;
  items?: IItem[];
  itemTitle?: string;
  itemValue?: string;
  noDataText?: string;
  pageSize?: boolean | number | string;
  search?: boolean;
  searchLabel?: string;
  menu?: boolean;
  focused?: boolean;
}

const props = withDefaults(defineProps<ISelectProps>(), {
  color: "accent",
  density: "compact",
  readonly: false,
  variant: "outlined",
  placeholder: "",
  persistentPlaceholder: false,
  required: false,
  hideMandatory: false,
  hideNoData: false,
  hideDetails: "auto",
  changed: false,
  loading: false,
  multiple: false,
  chips: false,
  modelValue: "",
  items: () => [],
  itemTitle: "title",
  itemValue: "value",
  pageSize: false,
  noDataText: undefined,
  search: false,
  searchLabel: undefined,
  focused: false,
});

const emit = defineEmits<{
  (event: "update:modelValue", value: any): void;
  (event: "update:menu", value: boolean): void;
}>();

const {
  localMenu,
  slotsList,
  slots,
  showChips,
  updatedProps,

  getItemTitle,
  removeItem,
  updateSelection,
  selectedItems,
} = useSelect(props, emit, "a-select");

const searchText = ref("");
const pageIndex = ref(0);
const filteredItems = computed(() => {
  if (!searchText.value) {
    return [...props.items];
  }
  const text = searchText.value.toLowerCase();
  return props.items.filter((item) =>
    String(getItemTitle(item)).toLowerCase().includes(text),
  );
});
const localPageSize = computed(() => {
  if (!props.pageSize) {
    return 0;
  }

  return props.pageSize === true ? DEFAULT_PAGE_SIZE : Number(props.pageSize);
});
const showPagination = computed(() => localPageSize.value > 0);
const pageItems = computed(() => {
  if (!showPagination.value) {
    return filteredItems.value;
  }

  const pageStart = pageIndex.value * localPageSize.value;
  return filteredItems.value.slice(pageStart, pageStart + localPageSize.value);
});

const pageCount = computed(() =>
  showPagination.value
    ? Math.max(Math.ceil(filteredItems.value.length / localPageSize.value), 1)
    : 1,
);

watch(pageCount, (count) => {
  if (count <= pageIndex.value) {
    pageIndex.value = count - 1;
  }
});

const extendedProps = computed(() => ({
  ...updatedProps.value,
  items: pageItems.value,
  menuProps:
    pageCount.value <= 1
      ? undefined
      : {
          maxHeight: (localPageSize.value + 4) * 48,
        },
}));

const showSearchInput = computed(() => props.search && props.items?.length);
const showPrependSlot = computed(
  () =>
    Boolean(slotsList["prepend-item"]) ||
    showChips.value ||
    showSearchInput.value,
);
const showAppendSlot = computed(() =>
  Boolean(slotsList["append-item"] || pageCount.value > 1),
);
</script>

<template>
  <div v-if="loading">
    <ASkeletonLoader
      :class="`a-input v-input a-select a-input--skeleton-${density}`"
    />
  </div>

  <v-select
    v-else
    v-model:menu="localMenu"
    v-bind="extendedProps"
    @update:model-value="updateSelection"
  >
    <template v-if="showPrependSlot" #prepend-item>
      <div class="a-list-item__sticky a-list-item__skipped">
        <v-list-item v-if="showSearchInput">
          <ASearchField
            v-model.trim="searchText"
            :label="searchLabel"
            autofocus
          />
        </v-list-item>

        <slot name="prepend-item" />

        <v-list-item v-if="showChips">
          <v-list-item-title class="a-select__selected-chips">
            <AChip
              v-for="selected of selectedItems"
              :key="selected"
              size="small"
              closable
              @click:close="removeItem(selected)"
            >
              {{ getItemTitle(selected) }}
            </AChip>
          </v-list-item-title>
        </v-list-item>
      </div>
    </template>

    <template v-if="showAppendSlot" #append-item>
      <div class="a-list-item__sticky a-list-item__skipped">
        <slot name="append-item" />

        <v-list-item v-if="showPagination">
          <APaginationBar
            v-model:page-count="pageCount"
            v-model:page-index="pageIndex"
          />
        </v-list-item>
      </div>
    </template>

    <template v-if="multiple || chips" #selection="{ item, index }">
      <v-chip
        v-if="index < 2"
        size="small"
        class="mr-1"
        closable
        @click:close="removeItem(item)"
        @click.stop
      >
        <span>{{ item.title }}</span>
      </v-chip>
      <span
        v-if="index === 2"
        class="text-primary text-caption align-self-center"
      >
        +{{ modelValue.length - 2 }}
      </span>
    </template>

    <template v-for="(_, slot) of slots" #[slot]="scope">
      <slot :key="slot" :name="slot" v-bind="scope" />
    </template>
  </v-select>
</template>

<style lang="scss">
.a-select {
  &--hide-selection.v-select--active-menu {
    .v-select__selection {
      display: none;
    }
  }

  .v-select__selection {
    margin: 0;
  }

  &__selected-chips {
    display: flex;
    flex-wrap: wrap;
    gap: 4px;
  }

  .v-chip {
    background-color: rgb(var(--v-theme-chip_bg));
    color: rgb(var(--v-theme-chip_color));
  }
}

.v-menu .v-overlay__content.v-select__content > .v-list {
  --v-theme-table_pagination_bg: var(--v-theme-on-background);
  padding-bottom: 0;
  padding-top: 0;
  background: rgb(var(--v-theme-on-background));
  position: relative;

  & > .v-list-item {
    color: rgb(var(--v-theme-list_color));

    &:not(.v-list-item--prepend):not(.v-list-item--append):hover,
    &--active {
      background: rgb(var(--v-theme-hover_positive_secondary_3));
    }

    .v-chip:not(:last-child) {
      margin-right: 4px;
    }
  }

  .v-chip {
    background-color: rgb(var(--v-theme-chip_bg));
    color: rgb(var(--v-theme-chip_color));

    &:not(:last-child) {
      margin-right: 4px;
    }
  }
}
</style>
