<script setup lang="ts">
import { nextTick, ref, watchEffect, watchPostEffect } from "vue";
import ASnackbar from "./ASnackbar.vue";
import { NotificationType } from "../constants";
import { AIcon } from "../AIcon";
import { ABtn } from "../ABtn";

interface INotification {
  title?: string;
  message: string;
  timeout?: number;
  type: NotificationType;
}

interface ISnackbar {
  id: number;
  title?: string;
  message: string;
  color: string;
  show: boolean;
  timer?: number;
}

const props = withDefaults(
  defineProps<{
    timeout?: number;
    modelValue: INotification[];
    margin?: number;
  }>(),
  {
    timeout: 5000,
    margin: 0,
  },
);

const emit = defineEmits<{
  (event: "update:modelValue", value: INotification[]): void;
}>();

const uniqId = String(Math.random()).slice(2, 6);
const snackbarsId = `v-snackbars-${Date.now()}-${uniqId}`;
let lastId = 0;

const snackbars = ref<ISnackbar[]>([]);

watchPostEffect(async () => {
  if (!snackbars.value.length) {
    return;
  }
  const snacksClass = `.v-overlay-container .${snackbarsId} .v-overlay__content`;
  const snacks = document.querySelectorAll<HTMLElement>(snacksClass);

  if (!snacks?.length) {
    return;
  }

  let prevBottom = 0;
  await nextTick;
  snacks.forEach((el) => {
    el.style.bottom = `${prevBottom}px`;
    prevBottom += el.clientHeight + props.margin;
  });
});

const removeSnackbar = (id: number) => {
  const index = snackbars.value.findIndex((snackbar) => snackbar.id === id);
  if (index < 0) {
    return;
  }

  const removed = snackbars.value[index];
  if (removed.timer) {
    clearTimeout(removed.timer);
  }

  snackbars.value.splice(index, 1);
};

watchEffect(() => {
  if (!props.modelValue.length) {
    return;
  }

  const newMessages = props.modelValue.map<ISnackbar>(
    ({ title, message, timeout, type }) => {
      const id = ++lastId;
      const currentTimeout = timeout ?? props.timeout;

      const timer =
        currentTimeout > 0
          ? setTimeout(removeSnackbar, currentTimeout, id)
          : undefined;

      return {
        id,
        title,
        message,
        color: String(type),
        show: true,
        timer,
      };
    },
  );
  snackbars.value.push(...newMessages);
  emit("update:modelValue", []);
});
</script>
<template>
  <ASnackbar
    v-for="snackbar of snackbars"
    :key="snackbar.id"
    v-model="snackbar.show"
    :color="snackbar.color"
    :class="`v-snackbars ${snackbarsId} v-snackbars--${snackbar.id}`"
    :timeout="-1"
    @click="removeSnackbar(snackbar.id)"
  >
    <div class="d-flex align-center">
      <AIcon icon="info" class="pr-6 pl-1" />
      <div>
        <div v-if="snackbar.title" class="v-snackbars--title">
          {{ snackbar.title }}
        </div>
        <span class="v-snackbars--text">
          {{ snackbar.message }}
        </span>
      </div>
    </div>
    <template #actions>
      <ABtn
        color="white"
        variant="text"
        text="OK"
        @click="snackbar.show = false"
      />
    </template>
  </ASnackbar>
</template>

<style lang="scss">
.v-snackbars {
  &--title {
    font-weight: bold;
  }

  .v-overlay__content {
    transition: bottom 500ms;
  }
}
</style>
