<script setup lang="ts">
import { computed, nextTick, ref, watchEffect, type Ref } from "vue";
import { useI18n } from "vue-i18n";
import ADialog from "./ADialog.vue";
import { ACard, ACardTitle, ACardActions } from "../ACard";
import { ABtn } from "../ABtn";

const props = withDefaults(
  defineProps<{
    title?: string;
    cancelCaption?: string;
    modelValue?: boolean;
    persistent?: boolean;
    theme?: string;
    paddingX?: string;
    paddingY?: string;
    scrollable?: boolean;
  }>(),
  {
    title: undefined,
    cancelCaption: undefined,
    modelValue: undefined,
    persistent: false,
    theme: "light",
    paddingX: "24px",
    paddingY: "16px",
    scrollable: true,
  },
);

const emit = defineEmits<{
  (event: "update:modelValue", isVisible: boolean): void;
}>();

const { t } = useI18n();

const actionsSlot = ref<ACardActions>();
const contentSlot = ref<HTMLDivElement>();

const localShow = ref(false);
const isVisible = computed({
  get() {
    return props.modelValue ?? localShow.value;
  },
  set(value) {
    localShow.value = value;
    emit("update:modelValue", value);
  },
});

const paddings = computed(() =>
  props.paddingY ? `margin: ${props.paddingY} ${props.paddingX};` : "",
);

const cancelText = computed(() =>
  props.cancelCaption === undefined ? t("common.cancel") : props.cancelCaption,
);

const closeDialog = (shouldClose: boolean) => {
  if (shouldClose || !props.persistent) {
    isVisible.value = false;
  }
};

const showDialog = () => {
  isVisible.value = true;
};

watchEffect(async () => {
  if (!isVisible.value) {
    return;
  }
  await nextTick();
  actionsSlot.value?.$el.querySelector(".a-btn__cta")?.focus();
});

defineExpose<{
  actionsSlot: Ref<ACardActions | undefined>;
  contentSlot: Ref<HTMLDivElement | undefined>;
}>({ actionsSlot, contentSlot });
</script>

<template>
  <ADialog
    v-model="isVisible"
    :persistent="persistent"
    :theme="theme"
    transition="dialog-bottom-transition"
  >
    <template #activator>
      <slot name="activator" :props="{ onClick: showDialog }" />
    </template>

    <ACard class="d-flex h-100 overflow-hidden">
      <ACardTitle v-if="title" class="d-flex align-center pl-6 ma-0">
        {{ title }}
      </ACardTitle>
      <div
        ref="contentSlot"
        :style="paddings"
        class="flex-grow-1 d-flex flex-column"
        :class="scrollable ? 'overflow-auto' : 'overflow-hidden'"
      >
        <slot />
      </div>
      <ACardActions
        v-if="cancelText || $slots.actions"
        ref="actionsSlot"
        class="justify-end"
      >
        <ABtn
          v-if="cancelText"
          color="on_dark"
          :text="cancelText"
          @click="closeDialog(true)"
        />
        <slot
          name="actions"
          :is-visible="isVisible"
          @click="closeDialog(false)"
        />
      </ACardActions>
    </ACard>
  </ADialog>
</template>

<style scoped lang="scss">
.dialog-bottom-transition-enter-active,
.dialog-bottom-transition-leave-active {
  transition: transform 0.2s ease-in-out;
}

.v-card {
  background-color: rgb(var(--v-theme-background_2));

  .v-card-title {
    height: 64px;
  }

  .v-card-actions {
    background: $gradient_2;

    .v-btn ~ .v-btn {
      margin-inline-start: 16px;
    }
  }
}
</style>
