<script setup lang="ts">
/* eslint-disable @typescript-eslint/no-explicit-any */
import { computed } from "vue";
import {
  ASkeletonLoader,
  AChip,
  AListItem,
  AListItemTitle,
  useSelect,
  type IListProps,
  type IItem,
} from "../index";

interface IComboboxProps extends IListProps {
  color?: string;
  density?: "default" | "comfortable" | "compact";
  readonly?: boolean;
  transparent?: boolean;
  variant?:
    | "outlined"
    | "plain"
    | "underlined"
    | "filled"
    | "solo"
    | "solo-inverted"
    | "solo-filled";
  placeholder?: string;
  persistentPlaceholder?: boolean;
  required?: boolean;
  hideMandatory?: boolean;
  hideNoData?: boolean;
  hideDetails?: boolean | string;
  changed?: boolean;
  loading?: boolean;
  multiple?: boolean;
  chips?: boolean;
  modelValue?: any;
  items?: IItem[];
  itemTitle?: string;
  itemValue?: string;
  noDataText?: string;
  returnObject?: boolean;
  menu?: boolean;
}

const props = withDefaults(defineProps<IComboboxProps>(), {
  color: "accent",
  density: "compact",
  readonly: false,
  variant: "outlined",
  placeholder: "",
  persistentPlaceholder: false,
  required: false,
  hideMandatory: false,
  hideNoData: false,
  hideDetails: "auto",
  changed: false,
  loading: false,
  multiple: false,
  chips: false,
  modelValue: "",
  items: () => [],
  itemTitle: "title",
  itemValue: "value",
  noDataText: undefined,
  returnObject: false,
  menu: false,
});

const emit = defineEmits<{
  (event: "update:modelValue", value: any): void;
  (event: "update:menu", value: boolean): void;
}>();

const {
  localMenu,
  selection,
  slotsList,
  slots,
  showChips,
  updatedProps,

  getItemTitle,
  removeItem,
  updateSelection,
  selectedItems,
} = useSelect(props, emit, "a-combobox");

const showPrependSlot = computed(
  () => Boolean(slotsList["prepend-item"]) || showChips.value,
);
const showAppendSlot = computed(() => Boolean(slotsList["append-item"]));
</script>

<template>
  <div v-if="loading">
    <ASkeletonLoader
      :class="`a-input v-input a-combobox a-input--skeleton-${density}`"
    />
  </div>

  <v-combobox
    v-else
    v-bind="updatedProps"
    @update:model-value="updateSelection"
    @update:menu="localMenu = $event"
  >
    <template v-if="showPrependSlot" #prepend-item>
      <div class="a-list-item__sticky a-list-item__skipped">
        <slot name="prepend-item" />

        <AListItem v-if="showChips">
          <AListItemTitle class="a-combobox__selected-chips">
            <AChip
              v-for="selected of selectedItems"
              :key="selected"
              size="small"
              closable
              @click:close="removeItem(selected)"
            >
              {{ getItemTitle(selected) }}
            </AChip>
          </AListItemTitle>
        </AListItem>
      </div>
    </template>

    <template v-if="showAppendSlot" #append-item>
      <div class="a-list-item__sticky a-list-item__skipped">
        <slot name="append-item" />
      </div>
    </template>

    <template v-if="multiple || chips" #selection="{ item, index }">
      <v-chip
        v-if="index < 2"
        size="small"
        class="mr-1"
        closable
        @click:close="removeItem(item)"
        @click.stop
      >
        <span>{{ item.title }}</span>
      </v-chip>
      <span
        v-if="index === 2"
        class="text-primary text-caption align-self-center"
      >
        +{{ selection.length - 2 }}
      </span>
    </template>

    <template v-for="(_, slot) of slots" #[slot]="scope">
      <slot v-bind="scope" :key="slot" :name="slot" />
    </template>
  </v-combobox>
</template>

<style lang="scss">
.v-combobox__mask {
  color: rgb(var(--v-theme-field_color));
}
.a-combobox {
  .v-field__input {
    --v-high-emphasis-opacity: 1;
    --v-medium-emphasis-opacity: 1;
  }
  &.v-combobox--active-menu {
    .v-combobox__selection {
      display: none;
    }
  }
  .v-combobox__selection {
    margin: 0;
  }

  &__selected-chips {
    display: flex;
    flex-wrap: wrap;
    gap: 4px;
  }

  .v-chip {
    background-color: rgb(var(--v-theme-chip_bg));
    color: rgb(var(--v-theme-chip_color));
  }
}

.v-menu .v-overlay__content.v-combobox__content > .v-list {
  padding-bottom: 0;
  padding-top: 0;
  position: relative;

  & > .v-list-item {
    &:not(.v-list-item--prepend):not(.v-list-item--append):hover,
    &--active {
      background: rgb(var(--v-theme-hover_positive_secondary_3));
    }
  }

  .v-chip {
    background-color: rgb(var(--v-theme-chip_bg));
    color: rgb(var(--v-theme-chip_color));

    &:not(:last-child) {
      margin-right: 4px;
    }
  }
}
</style>
