/* eslint-disable @typescript-eslint/no-explicit-any */
import { computed, ref, useSlots, watch, watchEffect } from "vue";
import { MESSAGES } from "..";

export type IItem = Record<string, any> | string;

export interface IListProps {
  color?: string;
  density?: "default" | "comfortable" | "compact";
  readonly?: boolean;
  transparent?: boolean;
  variant?:
    | "outlined"
    | "plain"
    | "underlined"
    | "filled"
    | "solo"
    | "solo-inverted"
    | "solo-filled";
  placeholder?: string;
  persistentPlaceholder?: boolean;
  required?: boolean;
  hideMandatory?: boolean;
  hideNoData?: boolean;
  hideDetails?: boolean | string;
  changed?: boolean;
  loading?: boolean;
  multiple?: boolean;
  chips?: boolean;
  modelValue?: any;
  items?: (IItem | string)[];
  itemTitle?: string;
  itemValue?: string;
  noDataText?: string;
  menu?: boolean;
}

const CUSTOM_SLOTS = ["prepend-item", "append-item", "no-data"];

export const useSelect = (
  props: IListProps,
  emit: {
    (event: "update:modelValue", value: any): void;
    (event: "update:menu", value: boolean): void;
  },
  componentClass: string,
) => {
  const getItemValue = (item: IItem | string) =>
    typeof item === "string" ? item : item[props.itemValue as keyof IItem];
  const getItemTitle = (item: IItem | string) =>
    typeof item === "string" ? item : item[props.itemTitle as keyof IItem];

  const slotsList = useSlots();
  const localMenu = ref(false);
  const selection = ref<any>([]);
  const slots = Object.entries(slotsList).filter(
    ([slotName]) => !CUSTOM_SLOTS.includes(slotName),
  );
  const showChips = computed(
    () => localMenu.value && props.multiple && selection.value.length,
  );
  const removeItem = (item: IItem) => {
    if (props.multiple) {
      const value = getItemValue(item);
      return updateSelection(
        (selection.value as any[]).filter((itemValue) => itemValue !== value),
      );
    }
    updateSelection(undefined);
  };

  const updateSelection = (selected?: any) => {
    selection.value = selected;
    emit("update:modelValue", selected);
  };

  const selectedItems = computed(() => {
    if (Array.isArray(selection.value)) {
      return props.items?.filter((item) =>
        (selection.value as any[]).includes(getItemValue(item)),
      );
    }

    return [];
  });

  watchEffect(() => {
    if (props.multiple) {
      selection.value = Array.isArray(props.modelValue)
        ? [...props.modelValue]
        : [];
    } else {
      selection.value = props.modelValue ? props.modelValue : null;
    }
  });

  watch(localMenu, (value) => emit("update:menu", value));
  watch(
    () => props.menu,
    (value) => {
      localMenu.value = Boolean(value);
    },
    { immediate: true },
  );

  const updatedProps = computed(() => ({
    closableChips: true,
    ...props,
    class: [
      componentClass,
      "a-input",
      {
        "v-input--filled": !props.transparent,
        "a-input--changed": props.changed,
        "a-input--required":
          !props.hideMandatory && props.required && !props.readonly,
        [`${componentClass}--hide-selection`]: showChips.value,
      },
    ],
    variant: props.readonly ? "plain" : props.variant,
    placeholder: props.readonly ? MESSAGES.MANDATORY_FIELD : props.placeholder,
    persistentPlaceholder: props.readonly || props.persistentPlaceholder,
    menuIcon: props.readonly ? "" : "$dropdown",
    modelValue: selection.value,
    menu: localMenu.value,
    noDataText: props.noDataText ?? MESSAGES.LIST_IS_EMPTY,
    centerAffix: false,
  }));

  return {
    localMenu,
    selection,
    slotsList,
    slots,
    showChips,
    updatedProps,

    getItemTitle,
    removeItem,
    updateSelection,
    selectedItems,
  };
};
