<script setup lang="ts">
import { computed, useModel } from "vue";
import ASkeletonLoader from "../ASkeletonLoader/ASkeletonLoader.vue";
import { useInputColor } from "../use/color";

const props = withDefaults(
  defineProps<{
    density?: string;
    hideDetails?: boolean | "auto";
    loading?: boolean;
    disabled?: boolean;
    readonly?: boolean;
    modelValue?: boolean;
    label?: string;
  }>(),
  {
    density: "compact",
    hideDetails: "auto",
    disabled: false,
    modelValue: false,
    loading: false,
    label: undefined,
  },
);

defineEmits<{
  (event: "update:modelValue", value: boolean): void;
}>();

const value = useModel(props, "modelValue");

const color = useInputColor(props);

const updatedProps = computed(() => ({
  ...props,
  trueIcon: props.readonly ? "done" : undefined,
  falseIcon: props.readonly ? "clear" : undefined,
}));
</script>

<template>
  <div v-if="loading" class="a-input--skeleton-compact d-flex align-center">
    <ASkeletonLoader
      type="icon"
      width="24px"
      height="24px"
      class="a-input v-input mr-2"
    />
    <ASkeletonLoader v-if="label" height="18px" class="a-input v-input" />
  </div>
  <v-radio
    v-else
    v-model="value"
    v-bind="updatedProps"
    :color="color"
    class="a-radio"
  >
    <template v-for="(_, slot) of $slots" #[slot]="scope">
      <slot :key="slot" :name="slot" v-bind="scope" />
    </template>
  </v-radio>
</template>

<style lang="scss">
.a-radio {
  &.v-selection-control {
    --v-selection-control-size: 40px;

    .v-label,
    &__input > .v-icon,
    &--disabled {
      opacity: 1;
    }

    &--disabled {
      color: rgb(var(--v-theme-disabled));
    }
  }
}
</style>
