<script setup lang="ts">
import { computed } from "vue";
import AIcon from "../AIcon/AIcon.vue";

const props = withDefaults(
  defineProps<{
    clickable?: boolean;
    color?: string;
    borderColor?: string;
    error?: boolean;
    errorIcon?: string;
    icon?: string;
    variant?: "mini" | "icon" | "default";
    width?: number | string;
    label?: string;
  }>(),
  {
    color: undefined,
    borderColor: undefined,
    errorIcon: "error",
    icon: undefined,
    variant: "default",
    width: undefined,
    label: "",
  },
);

const updatedProps = computed(() => ({
  class: [
    `a-flag a-flag--${props.variant} bg-${props.color}`,
    {
      "a-flag--clickable": props.clickable,
      "a-flag--has-icon": props.variant === "default" && props.icon,
      "a-flag--has-error": props.error,
    },
  ],
  style: {
    "--border-color": props.borderColor,
    width:
      props.width && props.variant === "default"
        ? `${props.width}px`
        : undefined,
  },
}));
</script>

<template>
  <div v-bind="updatedProps">
    <a-icon
      v-if="variant !== 'mini' && icon"
      :icon="icon"
      class="a-flag__icon"
    />
    <div class="a-flag__content">
      <slot>
        {{ label }}
      </slot>
    </div>
    <div v-if="error" class="a-flag__error bg-error">
      <a-icon
        v-if="variant === 'mini' || variant === 'default'"
        :icon="errorIcon"
        class="a-flag__error--icon"
      />
    </div>
  </div>
</template>

<style lang="scss">
