  font-family: RobotoCondensed, sans-serif;
  box-sizing: border-box;
  position: relative;
  display: flex;
  align-items: center;
  overflow: hidden;
  border: 1px solid var(--border-color, transparent);

  &__icon {
    display: block;
    margin: auto 10px;
  }

  &__error {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 6px;
    height: 100%;

    &--icon {
      display: block;
    }
  }

  &__content {
    margin: auto 4px;
    flex-grow: 1;
  }

  &--default {
    height: 32px;
    font-size: 0.875rem;
    line-height: 1rem;
    border-radius: 8px;
  }

  &--mini {
    line-height: 1;
    font-size: 0.75rem;
    border-radius: 4px;
    height: 20px;
    min-width: 20px;

    .a-flag__content {
      margin: auto 6px;
    }

    .a-flag__error {
      padding: 0 6px;

      .v-icon {
        font-size: 0.875rem;
      }
    }

    &.bg-on-background {
      border: 1px solid rgb(var(--v-theme-background));
    }

    &.bg-on_dark {
      border: 1px solid rgb(var(--v-theme-on_light));
    }

    &.bg-on_light {
      border: 1px solid rgb(var(--v-theme-on_dark));
    }
  }

  &--icon {
    width: 40px;
    height: 32px;
    border-radius: 8px;

    .a-flag__content {
      position: relative;
      height: 100%;
      opacity: 0;
    }

    .a-flag__error {
      width: 50%;
      height: 100%;
    }
  }

  &--clickable {
    cursor: pointer;
  }

  &.bg-background {
    border: 1px solid rgb(var(--v-theme-on-background));
  }
}
</style>
