<script setup lang="ts">
import { computed } from "vue";
import { MESSAGES } from "../constants";
import ASkeletonLoader from "../ASkeletonLoader/ASkeletonLoader.vue";

const props = withDefaults(
  defineProps<{
    color?: string;
    density?: string;
    hideDetails?: boolean | "auto";
    readonly?: boolean;
    transparent?: boolean;
    variant?: string;
    placeholder?: string;
    persistentPlaceholder?: boolean;
    required?: boolean;
    hideMandatory?: boolean;
    changed?: boolean;
    loading?: boolean;
  }>(),
  {
    color: "accent",
    density: "compact",
    hideDetails: "auto",
    variant: "outlined",
    readonly: false,
    placeholder: "",
    persistentPlaceholder: false,
    required: false,
    hideMandatory: false,
    changed: false,
    loading: false,
  },
);

const updatedProps = computed(() => ({
  class: [
    "a-autocomplete.a-input",
    {
      "v-input--filled": !props.transparent,
      "a-input--changed": props.changed,
      "a-input--required":
        !props.hideMandatory && props.required && !props.readonly,
    },
  ],
  color: props.color,
  density: props.density,
  readonly: props.readonly,
  variant: props.readonly ? "plain" : props.variant,
  placeholder: props.readonly ? MESSAGES.MANDATORY_FIELD : props.placeholder,
  persistentPlaceholder: props.readonly || props.persistentPlaceholder,
}));
</script>
<template>
  <div v-if="loading">
    <ASkeletonLoader
      :class="`a-input--skeleton-${density}`"
      class="a-input v-input"
    />
  </div>
  <v-autocomplete v-else v-bind="updatedProps">
    <template v-for="(_, slot) of $slots" #[slot]="scope">
      <slot :key="slot" :name="slot" v-bind="scope" />
    </template>
  </v-autocomplete>
</template>
<style lang="scss">
.v-autocomplete__mask {
  color: rgb(var(--v-theme-field_color));
}
.v-menu .v-overlay__content.v-autocomplete__content > .v-list {
  background: rgb(var(--v-theme-on-background));

  & > .v-list-item {
    color: rgb(var(--v-theme-on_dark));

    &:hover,
    &--active {
      background: rgb(var(--v-theme-hover_positive_secondary_3));
    }
  }
}
</style>
