<script setup lang="ts">
import { computed, ref } from "vue";
import { ATextField } from "../ATextField";
import { AProgressIndicatorCircular } from "../AProgressIndicatorCircular";
import type { PasswordStrength } from "./APasswordField.types";

const props = withDefaults(
  defineProps<{
    density?: string;
    hideDetails?: boolean | "auto";
    modelValue?: string;
    strength?: ((value: string) => PasswordStrength) | null;
  }>(),
  {
    modelValue: "",
    density: "default",
    hideDetails: "auto",
    strength: null,
  },
);

const isTextVisible = ref(false);

const passwordStrength = computed<Partial<PasswordStrength>>(() =>
  props.strength ? props.strength(props.modelValue) : {},
);
</script>

<template>
  <ATextField
    :class="{ 'a-text-field--has-circular-progress': strength }"
    :type="isTextVisible ? 'text' : 'password'"
    :density="density"
    prepend-inner-icon="lock"
    :append-inner-icon="isTextVisible ? 'visibility_off' : 'visibility'"
    required
    :model-value="modelValue"
    v-bind="$attrs"
    @click:append-inner="isTextVisible = !isTextVisible"
  >
    <AProgressIndicatorCircular
      v-if="strength"
      :color="passwordStrength.color"
      :size="density === 'default' ? 40 : 28"
      :model-value="passwordStrength.value"
    />
  </ATextField>
</template>

<style lang="scss">
.a-text-field--has-circular-progress {
  position: relative;

  .v-field__prepend-inner {
    padding-left: 6px;
    padding-right: 6px;
  }

  .a-progress-indicator-circular {
    position: absolute;
    left: -38px;
    top: 9px;
  }

  &.v-input--density-compact {
    .v-field__prepend-inner {
      //padding-top: 13px;
      padding-left: 4px;
      padding-right: 4px;

      .v-icon {
        font-size: 1rem;
      }
    }

    .a-progress-indicator-circular {
      position: absolute;
      left: -26px;
      top: 7px;
    }
  }
}
.v-field__append-inner {
  .v-icon {
    opacity: unset;
  }
}
</style>
