<script setup lang="ts">
import { computed, useModel } from "vue";
import ASkeletonLoader from "../ASkeletonLoader/ASkeletonLoader.vue";
import { useInputColor, useInputBgColor } from "../use/color";

const props = withDefaults(
  defineProps<{
    hideDetails?: boolean | "auto";
    density?: string;
    readonly?: boolean;
    disabled?: boolean;
    modelValue?: boolean;
    indeterminate?: boolean;
    rounded?: boolean;
    loading?: boolean;
    label?: string;
  }>(),
  {
    density: "compact",
    hideDetails: "auto",
    disabled: false,
    modelValue: false,
    indeterminate: false,
    rounded: false,
    loading: false,
    label: undefined,
  },
);

defineEmits<{
  (event: "update:modelValue", value: boolean): void;
}>();

const value = useModel(props, "modelValue");

const color = useInputColor(props);
const bgColor = useInputBgColor(props);

const updatedProps = computed(() => ({
  ...props,
  class: [
    "a-checkbox",
    {
      "a-checkbox--checked": props.modelValue,
      "a-checkbox--rounded": props.rounded,
    },
  ],
  trueIcon: props.readonly || props.rounded ? "done" : undefined,
  falseIcon: props.readonly
    ? "clear"
    : props.rounded
      ? "radio_button_unchecked"
      : undefined,
}));
</script>
<template>
  <div v-if="loading" class="a-input--skeleton-compact d-flex align-center">
    <ASkeletonLoader width="18px" height="18px" class="a-input v-input" />
    <ASkeletonLoader v-if="label" height="18px" class="a-input v-input ml-2" />
  </div>
  <v-checkbox
    v-else
    v-model="value"
    v-bind="updatedProps"
    :style="`--icon-color: var(--v-theme-${color});--icon-background: ${bgColor};`"
  >
    <template v-for="(_, slot) of $slots" #[slot]="scope">
      <slot :key="slot" :name="slot" v-bind="scope" />
    </template>
  </v-checkbox>
</template>
<style lang="scss">
.a-checkbox {
  .v-icon {
    color: rgba(var(--icon-color));
  }

  &--rounded {
    .v-icon {
      border-radius: 50%;
      background: var(--icon-background);
    }
  }

  .v-selection-control {
    --v-selection-control-size: 40px;

    .v-label,
    &__input > .v-icon,
    &--disabled {
      opacity: 1;
    }
  }
}
</style>
