<script setup lang="ts">
import { computed } from "vue";
import { withUnit } from "../use/units";

const props = withDefaults(
  defineProps<{
    type?: string;
    width?: string | number;
    height?: string | number;
    loading?: boolean;
    tag?: string;
  }>(),
  {
    type: "block",
    width: "",
    height: "",
    loading: true,
    tag: "div",
  },
);

const style = computed(() => {
  const style: Record<string, string | number> = {};
  if (props.width) {
    style["--skeleton-width"] = withUnit(String(props.width));
  }
  if (props.height) {
    style["--skeleton-height"] = withUnit(String(props.height));
  }
  return style;
});

// @todo: Not ready on Vuetify
// <v-skeleton-loader>
//   <template v-for="(_, slot) of $slots" #[slot]="scope">
//     <slot :key="slot" :name="slot" v-bind="scope" />
//   </template>
// <v-skeleton-loader>
</script>

<template>
  <component
    :is="tag"
    v-if="loading"
    class="a-skeleton-loader v-skeleton-loader"
    :class="`a-skeleton-loader__${type}`"
    :style="style"
  />
  <slot v-else />
</template>

<style lang="scss">
.a-skeleton-loader {
  &__block {
    --skeleton-width: 100%;
    --skeleton-height: 100%;
    --border-radius: 4px;
  }

  &__icon {
    --skeleton-width: 24px;
    --skeleton-height: 24px;
    --border-radius: 50%;
  }

  --v-theme-surface: var(--v-theme-skeleton);
  position: relative;
  width: var(--skeleton-width, 100%);
  height: var(--skeleton-height, 100%);
  border-radius: var(--border-radius, 4px);
  overflow: hidden;
  background: rgb(var(--v-theme-skeleton));

  &:after {
    content: "";
    position: absolute;
    height: 100%;
    left: 0;
    right: 0;
    top: 0;
    z-index: 1;
    animation: skeleton-loading 1.5s infinite;
    transform: translateX(-100%);
    background: $skeleton_animation_light;
  }
}

.v-theme--dark {
  .a-skeleton-loader {
    &:after {
      background: $skeleton_animation_dark;
    }
  }
}

@keyframes skeleton-loading {
  100% {
    transform: translateX(100%);
  }
}
</style>
