<script setup lang="ts">
import { computed } from "vue";
import { ASkeletonLoader, AProgressCircular } from "../index";

const props = withDefaults(
  defineProps<{
    showSkeleton?: boolean;
    loading?: boolean;
    icon?: boolean | string;
    disabled?: boolean;
    /** button will be focused if placed as action within ABaseDialog */
    cta?: boolean;
  }>(),
  {
    showSkeleton: false,
    loading: false,
    icon: undefined,
    disabled: false,
    cta: false,
  },
);

const isSpinnerLoading = computed(
  () => !props.disabled && props.loading && !props.showSkeleton,
);

function handleClick(event: MouseEvent) {
  if (isSpinnerLoading.value) {
    event.stopPropagation();
  }
}
</script>

<template>
  <v-btn
    class="a-btn"
    :icon="icon"
    :loading="!disabled && showSkeleton"
    :disabled="disabled"
    :class="{ 'a-btn__cta': cta }"
    @click.capture="handleClick"
  >
    <template v-if="isSpinnerLoading" #prepend>
      <AProgressCircular size="23" width="2" indeterminate />
    </template>

    <template v-if="showSkeleton" #loader>
      <ASkeletonLoader :type="icon ? 'icon' : 'block'" />
    </template>

    <template v-for="(_, slot) of $slots" #[slot]="scope">
      <slot :key="slot" :name="slot" v-bind="scope" />
    </template>
  </v-btn>
</template>

<style lang="scss">
.a-btn.v-btn {
  letter-spacing: inherit;

  .v-icon {
    --v-icon-size-multiplier: 1;
  }
  .v-btn__content {
    white-space: unset;
  }
  &:not(&--icon) {
    &.v-btn--size-x-large {
      --v-btn-size: 1.5rem;
      --v-btn-height: 56px;
      padding-top: 14px;
      padding-bottom: 14px;
    }
  }

  &--variant-flat,
  &--variant-elevated {
    color: rgba(var(--v-theme-primary));
  }

  &--icon {
    --v-btn-height: 28px;

    &.v-btn--size-x-large {
      --v-btn-height: 44px;
      --v-btn-size: 1rem;
    }
  }

  &:disabled,
  &.v-btn--disabled {
    opacity: 1;
    cursor: not-allowed;

    &.v-btn--variant-elevated,
    &.v-btn--variant-flat {
      background-color: rgb(var(--v-theme-disabled)) !important;
      color: rgb(var(--v-theme-surface));
    }

    &.v-btn--variant-outlined {
      border-color: rgb(var(--v-theme-disabled)) !important;
    }

    &.v-btn--variant-outlined,
    &.v-btn--variant-text {
      color: rgb(var(--v-theme-disabled)) !important;
    }

    .v-btn__overlay,
    .v-btn__underlay {
      opacity: 0;
    }
  }

  &.gradient_2 {
    background: $gradient_2;
    color: #fff;

    &.v-btn--disabled {
      background: $disabled;
    }
  }
}
</style>
